@import '../common/styles/sass/varibles';

.worksBlock {
  min-height: 100vh;

  .worksContainer {
    flex-direction: column;
    justify-content: flex-start;

    .myWorks {
      width: 100%;
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
      @media screen and (max-width: 755px) {
        justify-content: center;
      }
    }
  }
}

